
// import "./index.scss"
import Tab from "../../componet/tab";
import  { useState, useEffect } from 'react';  
import'./index.scss'
function Main() {  
  const [time, setTime] = useState(new Date().toLocaleTimeString());  
  
  useEffect(() => {  
    const timer = setInterval(() => {  
      setTime(new Date().toLocaleTimeString());  
    }, 1000);  
  
    return () => {  
      clearInterval(timer);  
    };  
  }, []); // 只在组件挂载时运行一次  
  
  return (  
    <div>  
      {/* 假设 Tab 是一个有效的组件 */}  
      <Tab />  
      <div style={{height:'95vh'}}>  
        <div id="clock">{ time }<img className="lanqiu" style={ { borderRadius: '60px',marginLeft:'50px' } } alt="篮球" src="assets/whiteB.JPG" ></img></div>
        <div className="mainIntroduce">
        <div style={{fontSize:"48px",color:"aliceblue",margin:"5px auto",width:"400px"}}>  
            张建羽的个人博客
            <p style={ { fontSize: "24px",color:"beige",textAlign:"center", borderBottom:"1px solid gray"}}>
            Everything is highly possible
          </p>
          </div>
          <br/>
        <p style={{fontSize:"24px",color:"white",width:"400px",margin:'0 auto'}}>  
            &nbsp;&nbsp;&nbsp;&nbsp;你好，很荣幸可以在茫茫人海中遇到你，本网站属于个人网站，其目的主要用于记录学习过程，以及学习过程中的经验分享，如果对你有所帮助，可以收藏本网站或在评论区与我互动，期待与您相识。
            
        </p> 
        </div>

      </div>  
    </div>  
  );  
}  
  
export default Main;

